<template>
  <v-card>
    <v-card-title>Weekly Form</v-card-title>
    <v-card-text>
      <v-form v-model="valid" ref="form" lazy-validation>
        <v-row dense>
          <v-col cols="12">
            <v-select
              single-line
              persistent-hint
              item-text="name"
              item-value="name"
              label="Event name"
              :items="scheduleNames"
              v-model="formData.name"
              :rules="[rules.required]"
            ></v-select>
          </v-col>
          <v-col cols="12">
            <v-slider label="Value" v-model="formData.value" max="100" min="0" thumb-label></v-slider>
          </v-col>
          <v-col cols="12">
            <v-select
              label="Days"
              item-value="value"
              multiple
              chips
              clearable
              :rules="[rules.required]"
              deletable-chips
              :items="dayList"
              item-text="displayName"
              v-model="formData.days"
            ></v-select>
          </v-col>
          <v-col cols="6">
            <v-menu
              ref="startMenu"
              v-model="showMenu"
              :close-on-content-click="false"
              :nudge-right="40"
              :return-value.sync="formData.start"
              transition="scale-transition"
              offset-y
              max-width="290px"
              min-width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  :rules="[rules.required]"
                  v-model="formData.start"
                  label="Start Time"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-time-picker
                v-if="showMenu"
                v-model="formData.start"
                full-width
                @click:minute="$refs.startMenu.save(formData.start)"
              ></v-time-picker>
            </v-menu>
          </v-col>

          <v-col cols="6">
            <v-menu
              ref="endMenu"
              v-model="showEndTime"
              :close-on-content-click="false"
              :nudge-right="40"
              :return-value.sync="formData.end"
              transition="scale-transition"
              offset-y
              max-width="290px"
              min-width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  :rules="[rules.required]"
                  v-model="formData.end"
                  label="End Time"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-time-picker
                v-if="showEndTime"
                v-model="formData.end"
                full-width
                @click:minute="$refs.endMenu.save(formData.end)"
              ></v-time-picker>
            </v-menu>
          </v-col>

          <v-col cols="12">
            <p class="v-label text-left">Event Color</p>
            <v-color-picker v-model="formData.color" mode="hexa"></v-color-picker>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn text @click="onCancel">Close</v-btn>
      <v-btn v-if="isEdit" color="error" :loading="isDeleting" text @click="onDeleteItem">Delete</v-btn>
      <v-btn color="primary" :loading="isSubmitting" text @click="onSaveForm">Save</v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import * as calendarUtil from './calendarUtil';

export default {
  name: 'weekly-form',
  props: ['onSave', 'onCancel', 'item', 'isEdit', 'config', 'timezone', 'onDelete'],
  data() {
    return {
      valid: false,
      showMenu: false,
      showEndTime: false,
      isDeleting: false,
      isSubmitting: false,
      formData: {
        name: '',
        days: [],
        start: null,
        end: null,
        color: '',
        value: null,
      },
      rules: {
        required: value => !!value || 'Required.',
        numberRule: v => !isNaN(parseFloat(v)) || 'Required.',
      },
      dayList: [
        { displayName: 'Sunday', value: 'sunday' },
        { displayName: 'Monday', value: 'monday' },
        { displayName: 'Tuesday', value: 'tuesday' },
        { displayName: 'Wednesday', value: 'wednesday' },
        { displayName: 'Thursday', value: 'thursday' },
        { displayName: 'Friday', value: 'friday' },
        { displayName: 'Saturday', value: 'saturday' },
      ],
    };
  },
  computed: {
    scheduleNames() {
      return Object.values((this.config && this.config.names) || {});
    },
  },
  mounted() {
    if (this.isEdit && this.item) {
      this.formData = {
        name: this.item.name,
        days: this.item.days,
        start: this.item.start,
        end: this.item.end,
        color: this.item.color,
        value: this.item.value,
      };
      this.formData = this.parseStartEndTime(this.formData);
    }
  },
  methods: {
    parseStartEndTime(formData) {
      return formData;
    },
    formatStartEndTime(formData) {
      return formData;
    },
    onSaveForm() {
      if (!this.$refs.form.validate()) {
        return;
      }
      this.isSubmitting = true;
      this.onSave(this.formatStartEndTime(this.formData), this.item ? this.item.id : null)
        .then(val => {
          this.onCancel();
        })
        .catch(() => {
          this.$toasted.show('Cannot create weekly event', { type: 'error' });
        })
        .finally(() => {
          this.isSubmitting = false;
        });
    },
    onDeleteItem() {
      this.isDeleting = true;
      this.onDelete(this.item.id)
        .then(() => {
          this.$toasted.show('Successfully deleted');
          this.onCancel();
        })
        .catch(err => {})
        .finally(() => {
          this.isDeleting = false;
        });
    },
  },
};
</script>
